/* App.css */

body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f8f8f8;
  overflow-x:hidden;
}

.top-navigation {
  background-color: #020202; /* Honey-like color */
  color: #ffffff;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.logo img {
  max-height: 60px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

li {
  font-weight: bold;
  font-size: 16px;
  margin: 0 50px; /* Add space around each item */
  cursor: pointer;
  transition: color 0.3s ease;
}

li:hover {
  color: #663300; /* Darker color on hover */
}

.container {
  max-width: 1200px;
  margin: 20px auto;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Add these styles to your CSS file */

/* Style for the container of the Zoom component */
.ZoomContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Adjust the height as needed */
}

/* Style for each image within the Zoom component */
.ZoomImage {
  width: 100%;
  max-width: 100%;
  object-fit: cover;
  border-radius: 8px; /* Add border-radius for rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Add a subtle box shadow for depth */
  transition: transform 0.3s ease-in-out; /* Add a smooth transition effect */
  min-height: 70vh;
  max-height: 100vh;
  object-position: center;

}

/* Style for the active image with indicators */
.ZoomImage.active {
  transform: scale(3); /* Scale the active image for zoom effect */
}
.shopButton {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  color: #fff;
  background-color: #d0db34;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

/* Change button color on hover */
.shopButton:hover {
  background-color: #2980b9;
}

